<template>
    <div class="content-container" direction="vertical">
        <div>
            <el-container class="content-row">
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.state"></el-input>
                </div>
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.name"></el-input>
                </div>
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.user"></el-input>
                </div>
            </el-container>
            <el-container class="content-row">
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-date-picker
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""
                    v-model="queryParams.sendTime">
                    </el-date-picker>
                </div>    
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-date-picker
                    type="daterange"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder=""
                    v-model="queryParams.payTime">
                    </el-date-picker>
                </div>  
            </el-container>
            <el-container class="content-row">
                <el-button type="primary" @click="search"></el-button>
                <el-button type="primary" @click="clear"></el-button>
                <el-button type="primary" @click="exportData"></el-button>
            </el-container>
        </div>
        <div>
            <el-table
            :data="orderList"
            tooltip-effect="dark"
            style="width: 100%">
                <el-table-column
                label=""
                width="100"
                prop="state">
                </el-table-column>
                <el-table-column
                label=""
                width="300"
                prop="name">
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="id">
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="manager">
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="count"
                >
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="price">            
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="coin"> 
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="payTime">
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="sendTime">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import Mock from '../../mock/Mock'
import Tools from '../../tools/Tools'
export default {
    data() {
        return {
            queryParams: {
                state:"",
                user:"",
                name:"",
                sendTime:"",
                payTime:""
            },
            orderList:[]
        }
    },
    mounted() {
        this.orderList = Mock.getManagerOrder();
    },
    methods: {
        search() {
            this.$message({
                type:'success',
                message:'' + JSON.stringify(this.queryParams)
            });
            this.managerList = Mock.getManagerOrder();
        },
        clear() {
            this.queryParams = {
                state:"",
                user:"",
                name:"",
                sendTime:"",
                payTime:""
            };
            this.managerList = Mock.getManagerOrder();
        },
        exportData() {
            Tools.exportJson('.json', JSON.stringify(this.orderList));
        }
    }
}
</script>