<template>
  <div class="content-container" direction="vertical">
      <!-- input -->
      <div>
          <el-container class="content-row">
              <div class="input-tip">
                  :
              </div>
              <div class="input-field">
                  <el-input v-model="queryParam.good"></el-input>
              </div>
              <div class="input-tip">
                  :
              </div>
              <div class="input-field">
                  <el-input v-model="queryParam.consignee"></el-input>
              </div>
              <div class="input-tip">
                  :
              </div>
              <div class="input-field">
                  <el-date-picker
                  type="daterange"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                  v-model="queryParam.payTime">
                  </el-date-picker>
              </div>
          </el-container>
          <el-container class="content-row">
              <div class="input-tip">
                  :
              </div>
              <div class="input-field">
                  <el-input v-model="queryParam.name"></el-input>
              </div>
              <div class="input-tip">
                  :
              </div>
              <div class="input-field">
                  <el-input v-model="queryParam.phone"></el-input>
              </div>
              <div class="input-tip">
                  :
              </div>
              <div class="input-field">
                  <el-date-picker
                  type="daterange"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                  v-model="queryParam.sendTime">
                  </el-date-picker>
              </div>                
          </el-container>
      </div>
      <div class="content-row">
          <el-container>
              <el-button type="primary" @click="requestData"></el-button>
              <el-button type="danger" @click="clear"></el-button>
              <el-button type="primary" @click="exportData"></el-button>
              <el-button type="primary" @click="dispatchGoods"></el-button>
              <el-button type="primary" @click="exportDispatchGoods"></el-button>
          </el-container>
      </div>
      <!-- list -->
      <div>
          <el-tabs type="card" @tab-click="handleClick">
              <el-tab-pane label=""></el-tab-pane>
              <el-tab-pane label=""></el-tab-pane>
              <el-tab-pane label=""></el-tab-pane>
              <el-tab-pane label=""></el-tab-pane>
              <el-tab-pane label=""></el-tab-pane>
              <el-tab-pane label=""></el-tab-pane>
          </el-tabs>
          <el-table
          ref="multipleTable"
          :data="orderList"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange">
              <el-table-column
              type="selection"
              width="55">
              </el-table-column>
              <el-table-column
              label=""
              width="100"
              prop="name">
              </el-table-column>
              <el-table-column
              label="/"
              width="100"
              prop="price">
              </el-table-column>
              <el-table-column
              label=""
              width="100"
              prop="buyer">
              </el-table-column>
              <el-table-column
              label=""
              width="200"
              prop="time">
              </el-table-column>
              <el-table-column
              label=""
              width="100"
              >
                  <template #default="scope">
                      <el-tag size="medium" :type="scope.row.role ? '' : 'info'">{{ scope.row.role ? '' : '' }}</el-tag>          
                  </template>
              </el-table-column>
              <el-table-column
              label=""
              width="100">
                  <template #default="scope">
                      <el-tag size="medium" :type="scope.row.state ? 'success' : 'danger'">{{ scope.row.state ? '' : '' }}</el-tag>          
                  </template>               
              </el-table-column>
              <el-table-column
              label=""
              width="200">
                  <template #default="scope">
                      <el-button size="mini" type="danger" @click="deleteItem(scope.$index)"></el-button>
                      <el-button size="mini" type="primary" @click="callUser(scope.row)"></el-button>            
                  </template>     
              </el-table-column>
              <el-table-column
              label=""
              width="100">
                  <template #default="scope">
                      <el-tag size="medium">{{ scope.row.payType ? '' : '' }}</el-tag>          
                  </template>
              </el-table-column>
              <el-table-column
              label=""
              width="200"
              prop="source">
              </el-table-column>
          </el-table>
      </div>
      
  </div>
</template>

<script>
import Mock from '../../mock/Mock.js'
import Tools from '../../tools/Tools.js'
export default {
  data() {
      return {
          // 
          orderList:[],
          // 
          queryParam:{
              good:"",
              consignee:"",
              phone:"",
              name:"",
              payTime:"",
              sendTime:""
          },
          // 
          multipleSelection:[]
      }
  },
  mounted () {
      this.orderList = Mock.getOrder(this.$route.params.type);
  },
  // 
  beforeRouteUpdate (to) {
      this.orderList = Mock.getOrder(to.params.type);
  },
  methods : {
      // 
      requestData() {
          this.$message({
              type:'success',
              message:'' + JSON.stringify(this.queryParam)
          })
          this.orderList = Mock.getOrder(this.$route.params.type);
      },
      // Tab 
      handleClick(tab) {
          this.$message({
              type:'success',
              message:'tab' + tab.props.label
          })
          this.orderList = Mock.getOrder(this.$route.params.type);
      },
      // 
      clear() {
          this.queryParam = {
              good:"",
              consignee:"",
              phone:"",
              name:"",
              payTime:"",
              sendTime:""
          }
          this.orderList = Mock.getOrder(this.$route.params.type);
      },
      // 
      exportData() {
          Tools.exportJson('.json', JSON.stringify(this.orderList));
      },
      // 
      exportDispatchGoods() {
          Tools.exportJson('.json', JSON.stringify(this.multipleSelection));
      },
      // 
      handleSelectionChange(val) {
          this.multipleSelection = val;
      },
      // 
      dispatchGoods() {
          this.$message({
              type:'success',
              message:'' + JSON.stringify(this.multipleSelection)
          })
      },
      // 
      deleteItem(item) {
          this.orderList.splice(item, 1)
      },
      // 
      callUser(item) {
          console.log(item)
          this.$message({
              type:'success',
              message:'' + item.phone
          })
      }
  }
}
</script>
<style>
</style>