<template>
    <div class="content-container" direction="vertical">
        <!-- input -->
        <div>
            <el-container class="content-row">
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.name"></el-input>
                </div>
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.id"></el-input>
                </div>
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-select v-model="queryParams.category" placeholder="">
                        <el-option v-for="item in categorys" :key="item" :label="item" :value="item">
                        </el-option>
                    </el-select>
                </div>
            </el-container>
            <el-container class="content-row">
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-select v-model="sellModeString">
                        <el-option key="0" label="" :value="0"></el-option>
                        <el-option key="1" label="" :value="1"></el-option>
                        <el-option key="2" label="" :value="2"></el-option>
                    </el-select>
                </div>
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-select v-model="expModeString">
                        <el-option key="0" label="" :value="0"></el-option>
                        <el-option key="1" label="" :value="1"></el-option>
                        <el-option key="2" label="" :value="2"></el-option>
                    </el-select>
                </div>
            </el-container>
        </div>
        <!-- button -->
        <div class="content-row">
            <el-container>
                <el-button type="primary" @click="requestData"></el-button>
                <el-button type="primary" @click="clear"></el-button>
                <el-button type="success" @click="addGood"></el-button>
            </el-container>
        </div>
        <!-- list -->
        <div>
            <el-table
            :data="goodsData"
            tooltip-effect="dark"
            style="width: 100%">
                <el-table-column
                label=""
                width="100">
                    <template #default="scope">
                        <div style="text-align:center"><el-image :src="scope.row.img" style="width: 60px; height: 100px"/></div>
                        <div style="text-align:center">{{scope.row.name}}</div>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="price">
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="sellCount">
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="count">
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="back">
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="backPrice">
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="name">
                    <template #default="scope">
                        <el-button @click="operate(scope.row)" :type="scope.row.state ? 'danger':'success'">{{scope.row.state ? '':''}}</el-button>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                width="100"
                prop="owner">
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="time">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>

<script>
import Mock from '../../mock/Mock'
export default {
    data() {
        return {
            goodsData:[],
            // 
            categorys:[
                "",
                "",
                ""
            ],
            queryParams:{
                name:"",
                id:"",
                category:"",
                sellMode:2, //0  1 2
                expMode:2,
            }
        }
    },
    computed: {
        sellModeString: {
            get() {
                if (this.queryParams.sellMode == 2) {
                    return ''
                }
                return this.queryParams.sellMode == 0 ? '' : ''
            },
            set(val) {
                this.queryParams.sellMode = val
            }
        },
        expModeString: {
            get() {
                if (this.queryParams.expMode == 2) {
                    return ''
                }
                return this.queryParams.expMode == 0 ? '' : ''
            },
            set(val) {
                this.queryParams.expMode = val
            }
            
        }
    },
    // 
    mounted () {
        this.goodsData = Mock.getGoods(this.$route.params.type);
    },
    // 
    beforeRouteUpdate (to) {
        this.goodsData = Mock.getGoods(to.params.type);
    },
    methods: {
        // 
        requestData() {
            this.$message({
                type:'success',
                message:'' + JSON.stringify(this.queryParams)
            })
            this.goodsData = Mock.getGoods(this.$route.params.type);
        },
        // 
        operate(item) {
            item.state = !item.state;
        },
        // 
        clear() {
            this.queryParams = {
                name:"",
                id:"",
                category:"",
                sellMode:2, 
                expMode:2,
            }
            this.goodsData = Mock.getGoods(this.$route.params.type);
        },
        // 
        addGood() {
            this.$router.push({name:'AddGood',params:{type:this.$route.params.type}})
        }
    }
}
</script>