<template>
    <div class="content-container" direction="vertical">
        <div>
            <el-container class="content-row">
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.state"></el-input>
                </div>
                <div class="input-tip">
                    :
                </div>
                <div class="input-field">
                    <el-input v-model="queryParams.info"></el-input>
                </div>
            </el-container>
            <el-container class="content-row">
                <el-button type="primary" @click="search"></el-button>
                <el-button type="primary" @click="clear"></el-button>
            </el-container>
        </div>
        <div>
            <el-table
            :data="managerList"
            tooltip-effect="dark"
            style="width: 100%">
                <el-table-column
                label=""
                width="300"
                prop="people">
                </el-table-column>
                <el-table-column
                label=""
                width="100">
                    <template #default="scope">
                          <el-tag :type="scope.row.state ? 'success' : 'warning'">{{scope.row.state ? '' : ''}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="time">
                </el-table-column>
                <el-table-column
                label=""
                width="200"
                prop="reqTime">
                </el-table-column>  
                <el-table-column
                label=""
                width="100"
                prop="reqTime">
                    <template #default='scope'>
                        <el-button size="mini" @click='click(scope.row)' :type="scope.row.state ? 'danger' : 'success'">{{scope.row.state ? '' : ''}}</el-button>
                    </template>
                </el-table-column>              
            </el-table>
        </div>
    </div>
</template>

<script>
import Mock from '../../mock/Mock'
export default {
    data() {
        return {
            queryParams: {
                state:"",
                info:""
            },
            managerList:[]
        }
    },
    mounted() {
        this.managerList = Mock.getManagerReqList();
    },
    methods: {
        search() {
            this.$message({
                type:'success',
                message:'' + JSON.stringify(this.queryParams)
            });
            this.managerList = Mock.getManagerReqList();
        },
        clear() {
            this.queryParams = {
                state:"",
                info:""
            };
            this.managerList = Mock.getManagerReqList();
        },
        click(item) {
            item.state = !item.state;
        }     
    }
}
</script>